import React from 'react'; import ReactDOM from 'react-dom'; import { HashRouter } from 'react-router-dom';
// -----------------------------------
import Cyberial from './App'; 
// Async function support 
import regeneratorRuntime from "regenerator-runtime";
// -----------------------------------Redux
import { createStore } from 'redux';
import { Provider } from 'react-redux'; 
import rootReducer from '~/Redux/indexReducers';
const store = createStore(rootReducer);
// -----------------------------------
ReactDOM.render(
  <Provider store={store}>
    <HashRouter>
        <Cyberial />
    </HashRouter>
  </Provider>
,document.getElementById('ReactComponentsContainer')
);